* {
  list-style: no;
  transition: all 0.5s;
}
li {
  list-style: none;
  margin: 0px;
  padding: 0px;
  text-align: start;
}
ul {
  // border: 2px solid black;
  padding: 0px;
}
.footer_container {
  background: rgb(237, 27, 36);
  color: white;
}
.navigation {
  background: #ed1b24;
  height: 100px;
  color: white;
  p {
    color: white;
  }
}

#li {
  color: white;
  text-decoration: underline;
}
#li1 {
  text-decoration: underline;
}
.card1 {
  border: 2px solid black;
}
.card1:hover {
  border: 2px solid black;
  transform: scale(1.2);
}

.products {
  font-family: 'Anek Malayalam', sans-serif;
  background-color: rgba(237, 27, 36, 0.06);
  .products1{
    padding: 10px 0px;
    .product {
      height: 500px;
      // border: 2px solid black;
      padding: 20px;
      margin: 30px;
      background-color: white;
      color: black;
      border-radius: 10px;
      .img {
        width: 100%;
        height: 250px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .name{
        font-size: 20px;
        font-weight: bold;
        margin: 10px 0px;
      }
      .disc{
        margin: 15px 0px;
        padding: 10px 0px;
        div{
          font-size: 15px;
          font-weight: bold;
          color: black;
        }
      }
      font-variant: small-caps;
      .price{
        font-size: 20px;
        font-weight: bold;
        color: black;
        display: flex;
        justify-content: space-between;
        .wholesale{
          font-size: 15px;
          font-weight: bold;
          color: white;
          background-color: green;
          padding: 5px;
        }
        .retail{
          padding: 5px;
          font-size: 15px;
          font-weight: bold;
          color: white;
          background-color: red;
          
        }
        margin: auto;
      }
    }
    
  }
  .inquirybutton{
    color: white;
    background: red;
    margin-top: 10px;
    width: 100%;
    border: none;
    border-radius: 0%;
  }
  .inquiry{
    background: rgba(red, 0.8);
    color: white;
    display: none;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1000;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    .closebutton{
      position: absolute;
      top: 0px;
      left: 0px;
      margin: 50px;
      font-size: 30px;
      color: white;
      cursor: pointer;
    }
  }
}
